import React, {useState} from 'react';
import Content from "../Content/Content";
import Sidebar from "../Sidebar/Sidebar";
import Pages from "../Pages/Pages";
import Navbar from "../Navbar/Navbar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Page404 from "./Page404";
import ScrollButton from "./Scroll-Button";
import Footer from "../Footer/Footer";

function HomePage() {
    const [visible, setVisible] = useState(true);
    function toggle() {
        setVisible(!visible);
    }
    return (
        <div>
            <ScrollButton scrollStepInPx="20" delayInMs="16.66"/>
            <Router forceRefresh={false} onUpdate={() => window.scrollTo(0, 0)} basename="/portfolio/">
                <Switch>
                    <Route exact path="/">
                        <Sidebar toggle={toggle} visible={visible} setVisible={setVisible}/>
                        <Content toggle={toggle} visible={visible} setVisible={setVisible}/>
                    </Route>
                    <Route path="/pages">
                        <Navbar/>
                        <Pages/>
                        <Footer/>
                    </Route>
                    <Route path="*">
                        <Page404/>
                    </Route>
                </Switch>
            </Router>
        </div>
    );
}

export default HomePage;
